.page-body {
  margin: 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}
.heading-specific {
  font-family: "Arial", "Montserrat", sans-serif;
  font-weight: 700;
}


.container {
  max-width: 1280px;
  margin-bottom: 100px;
  padding: 25px 96px;

  @media (max-width: $tablet-width) {
    max-width: 480px;
    padding: 0 20px;
  }
}

.promo__title {
  margin: 0 auto;
  font-size: 48px;
  max-width: 513px;
  text-align: center;
  line-height: 50px;
  color: $promo__title;

  @media (max-width: $tablet-width) {
    max-width: 480px;
    margin-bottom: 25px;
    padding: 0;
    text-align: left;
    font-size: 32px;
    line-height: 28px;
    text-transform: uppercase;
  }
}

.promo__text {
  padding: 25px 139.5px 40px;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  text-align: center;
  color: $promo__title;

  @media (max-width: $tablet-width) {
    margin-bottom: 30px;
    padding: 0;
    font-size: 16px;
    text-align: left;
  }
}

.promo__form {
  display: flex;
  gap: 50px;
  width: 100%;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 25px;
    box-sizing: border-box;
    border-radius: 10px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 320px;
  width: 100%;
  padding: 32px 22px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid $promo__title;
}

.form__title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 32px;
  text-align: center;
  color: $promo__title;
}

.form input {
  border: 1px solid $border-color;

  @media (max-width: $tablet-width) {
    display: flex;
    padding: 12px 10px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 5px;
  }
}

.form input {
  padding: 12px 10px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  background: 0 0;
  border-radius: 5px;
  color: $border-color;
}

.promo__block-video {
  position: relative;
  width: 100%;
  padding: 37.37% 0 0;

  @media (max-width: $tablet-width) {
    padding: 56.37% 0 0;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  background-color: $btn-color;
  border: 0;
  border-radius: 5px;
  color: $border-color;
}

.partners {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
}

.partners__title {
  margin-bottom: 50px;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: $promo__title;
}

.partners__list {
  display: flex;
  gap: 75px;
  flex-wrap: wrap;
  align-items: center;

  @media (max-width: $tablet-width) {
    gap: 25px;
    justify-content: space-between;
  }
}